
.sign_up_container {
  box-shadow: 1px 1px 5px 2px #f3f3f3;
  margin: 10px;
  display: flex;
  width: 350px;
  height: 750px;
  flex-direction: column;
  justify-content: space-around;
  background: linear-gradient(to top,rgb(181, 200, 254),rgb(63, 185, 255));
  border-radius: 10px;
}

.password_container{
  display: flex;
  align-items: center;
}
.password_icon{
  font-size: 18px;
  padding-right: 10px;
}
.error {
  text-align: center;
}
